import { publish, store } from '../store';

const autoFillForget = types => {
  const autoFill = { ...store.autoFill };
  types.forEach(type => delete autoFill[type]);
  const { username, password, otp } = autoFill;
  publish({ autoFill: username || password || otp ? autoFill : {} });
};

export default autoFillForget;
